import { Injectable } from '@angular/core';
import { VendorContact } from '../model/Vendor';
import { HttpHeaders, HttpClient } from '@angular/common/http';
import { environment } from 'src/environments/environment';
import { Observable } from 'rxjs';

@Injectable({
  providedIn: 'root'
})
export class AddvendorService {
  formData:VendorContact;

  constructor(private http:HttpClient) { }
  getAllVendors():Observable<VendorContact[]>{
    return this.http.get<VendorContact[]>
    (environment.APIUrl+'/vendors/null');      
  }
  getVendorById(vendorId:any):Observable<VendorContact>{
    return this.http.get<VendorContact>(environment.APIUrl+'/vendor/'+vendorId);
  }
  insertVendor(vendor:VendorContact){
    let body = JSON.stringify(vendor);
    console.log(vendor);
    let headers = new HttpHeaders({'Content-Type': 'application/json'});
    let options = { headers: headers }
    console.log('YES');
    if(vendor.vendorId){
      return this.http.put(environment.APIUrl+'/updatevendor/',body,options);
    }
    else{
    
      return this.http.post(environment.APIUrl +'/savevendor/', body, options);
    }
}
}
