import { Component, OnInit } from '@angular/core';
import { ViewvendorService } from './viewvendor.service';
import { Router } from '@angular/router';
import { VendorContact } from '../model/Vendor';

@Component({
  selector: 'app-viewvendor',
  templateUrl: './viewvendor.component.html',
  styleUrls: ['./viewvendor.component.scss']
})
export class ViewvendorComponent implements OnInit {
  vendors:VendorContact[];
  v=new VendorContact();
  item:any;
  searchId:any;

  constructor(private service:ViewvendorService,private router:Router) { }

  ngOnInit() {
    this.getVendor()
  }

  getVendor():void{
    this.service.getAllVendors()
    .subscribe((data)=>{
      this.vendors=data;
      console.log(data);

    },(error)=>{
      console.log(error);

    });
  }


  viewcontact(id:any){
    this.router.navigate(['viewperson' ,id]);
  }
  // to pass id
  PassId(searchvendor:any):void{
    this.searchId=searchvendor.vendorId;
  }

  // to disable vendor
  DisableVendor(sId:any):void{
    this.service.disableVendor(this.searchId)
    .subscribe((response)=>{
      console.log(response);
      this.getVendor();
    })
  }

  addVendor1(){
    this.router.navigate(['saveVendor']);
  }
  addVendor(id:any){
    this.router.navigate(['saveVendor',id]);
  }
  logout(){
    this.router.navigate(['login'])
  }

}
