import { Component, OnInit } from '@angular/core';
import { ViewcontactService } from './viewcontact.service';
import { VendorContact } from '../model/Vendor';
import { Router, ActivatedRoute } from '@angular/router';
import { AppComponent } from '../app.component';


@Component({
  selector: 'app-viewcontact',
  templateUrl: './viewcontact.component.html',
  styleUrls: ['./viewcontact.component.scss']
})
export class ViewcontactComponent implements OnInit {
vendors:VendorContact[];
vendor= new VendorContact();
vendorId:any;
  constructor(private service:ViewcontactService, private route:ActivatedRoute , private app:AppComponent,private router:Router ) { }

  ngOnInit() {
    this.getVendorById()
  }
  getVendorById(){
    this.vendorId=this.route.snapshot.paramMap.get('id');
    this.service.getVendorbyId(this.vendorId).subscribe((data)=>{
      this.vendor=data;
      console.log(data);
    },(error)=>{
      console.log(error);

    });
    }
    back(){
      this.router.navigate(['viewvendor']);
    }
    logout(){
      this.router.navigate(['login'])
    }
  }


