import { Component, OnInit } from '@angular/core';
import { AddvendorService } from './addvendor.service';
import { VendorContact } from '../model/Vendor';
import { Router, ActivatedRoute } from '@angular/router';
import { NgForm } from '@angular/forms';

@Component({
  selector: 'app-addvendor',
  templateUrl: './addvendor.component.html',
  styleUrls: ['./addvendor.component.scss']
})
export class AddvendorComponent implements OnInit {
  vendors:VendorContact[];
  vendor=new VendorContact();
  vendorId: any;

  constructor(private service:AddvendorService, private router:Router, private route:ActivatedRoute) { }

  ngOnInit() {
    this.route.params.subscribe(params => this.getVendorById(params['id']));
  }
  addVendor():void{
    this.service.insertVendor(this.vendor)
    .subscribe((response) => {
      console.log(response);
       this.getVendor;
      this.router.navigate(['viewvendor'])
      this.resetForm();
    },(error) => {
      console.log(error);
    });
  }
  resetForm(form?:NgForm){
    if(form!=null)
    form.resetForm();
    this.service.formData={
      
      vendorId: '',
      vendorName:'',
      address: '',
      location: '',
      service: '',
      pincode: '',
      isActive: '',
      contactId: '',
      department: '',
      email: '',
      phone : '' ,
      name:''          
    }                                  
  }
  getVendor():void{
    this.service.getAllVendors()
    .subscribe((data)=>{
      this.vendors=data;
      console.log(data);

    },(error)=>{
      console.log(error);

    });
  }
  getVendorById(vendorId:any){

    this.vendorId=vendorId;
    console.log(this.vendorId);
    this.service.getVendorById(this.vendorId).subscribe((vendorById)=>{
      this.vendor =vendorById;
    },(error)=>{
      console.log(error);
    });
    
  }
  backToVendor(){
    this.router.navigate(['viewvendor'])
  }
  logout(){
    this.router.navigate(['login'])
  }
  

}
