import { Injectable } from '@angular/core';

import { Observable } from 'rxjs';
import { VendorContact } from '../model/Vendor';
import { environment } from 'src/environments/environment';
import { HttpClient, HttpHeaders } from '@angular/common/http';


@Injectable({
  providedIn: 'root'
})
export class ViewvendorService {

  constructor(private http:HttpClient) { }
  getAllVendors():Observable<VendorContact[]>{
    return this.http.get<VendorContact[]>
    (environment.APIUrl+'/vendors/null')

  }
  disableVendor(vendorId:any){
    let body = JSON.stringify(vendorId);
      let headers = new HttpHeaders({'Content-Type': 'application/json'});
      let options= { headers:headers}
      return this.http.put<VendorContact[]>(environment.APIUrl+'/disablevendor/'+vendorId,body,options);
  }

  
  
}
